html{
  height: 100%;
  max-height: none;
  max-width: none;
}
.container {
  padding: 1rem;
}
div{
  max-width: inherit;
  max-height: inherit;
}
body {  
  font-family: -apple-system, 
    BlinkMacSystemFont, 
    "Segoe UI", 
    Roboto, 
    Oxygen-Sans, 
    Ubuntu, 
    Cantarell, 
    "Helvetica Neue", 
    sans-serif;
  font-weight: normal;
  margin: 0;
  max-height: 100%;
  height: 100%;
  width: 100%;
}
.chat-app-wrapper{
  border-style: dotted;
  width: 1000px;
  height: 500px;
}
.pannel-wrapper{
  height: inherit;
}
.tab-content{
  height: calc(100% - 58px);
}
.tab-content::-webkit-scrollbar {
  width: 5px;
}
.tab-content::-webkit-scrollbar-thumb{
  background-color: #A8A8A8;
  border-radius: 1rem;
}
#RoomTabs-tabpane-Chat_Main{
  height: 100%;
}

.chat-main {
  position: relative;
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  padding-left: 4px;
  padding-right: 4px;
  padding-bottom: 4px;
}
.user-list{
  width: 300px;
  height:100%;
  margin-right: 5px;
}
.user-list-users{
  overflow: auto;
  height: 100%;
}
.user-list-users::-webkit-scrollbar {
  width: 5px;
}
.user-list-users::-webkit-scrollbar-thumb{
  background-color: #A8A8A8;
  border-radius: 1rem;
}
.messages_wrapper{
  border: 1px solid rgba(0,0,0,.125);
  border-radius: .25rem;
  flex-direction: row;
  height: 100%;
  width: calc(100% - 300px);
}
.messages{
  height: 100%;
  width: 100%;
}
.form{
  background-color: whitesmoke;
  width: 100%;
  padding: 0.5rem 1rem ;
  position: relative;
  bottom: 0; left: 0; right: 0;
  backdrop-filter: blur(10px);
  display: inline-flex;
  flex-direction: column-reverse;
  box-sizing: border-box;
  outline: none;
}

.imessage li {
  list-style-type: none;
}
.imessage {
  background-color: #fff;
  border: 0px solid #e5e5ea;
  margin-left: 0;
  margin-right: 0px;
  width: 100%;
  height: calc(100% - 54px);
  display: flex;
  flex-direction: column;
  font-family: "SanFrancisco";
  font-size: 1.25rem;
  overflow: auto;
  padding: 0.5rem 1.5rem;
  box-sizing: border-box;
}
.imessage::-webkit-scrollbar {
  width: 5px;
}
.imessage::-webkit-scrollbar-thumb{
  background-color: #A8A8A8;
  border-radius: 1rem;
}
.imessage p {
  border-radius: 1.15rem;
  line-height: 1.25;
  max-width: 75%;
  padding: 0.5rem .875rem;
  position: relative;
  word-wrap: break-word;
  display: flexbox;
}

.imessage p::before,
.imessage p::after {
  bottom: -0.1rem;
  content: "";
  height: 1rem;
  position: absolute;
}
.message {
  display: flex;
  flex-direction: column;
}
span.from-me {
  text-align: right;
}
span.from-them {
  text-align: left;
}
p.from-me {
  align-self: flex-end;
  background-color: #248bf5;
  color: #fff; 
}

p.from-me::before {
  border-top-left-radius: 0.8rem 0.7rem;
  border-right: 1rem solid #248bf5;
  right: -0.35rem;
  top: 0rem;
  transform: translate(0, -0.1rem);
}

p.from-me::after {
  background-color: rgb(255, 255, 255);
  border-top-left-radius: 0.5rem;
  right: -40px;
  top: 0rem;
  transform:translate(-30px, -0px);
  width: 10px;
}

p[class^="from-"] {
  margin: 0.5rem 0;
  width: fit-content;
}

p.from-me ~ p.from-me {
  margin: 0.25rem 0 0;
}

p.from-me ~ p.from-me:not(:last-child) {
  margin: 0.25rem 0 0;
}

p.from-me ~ p.from-me:last-child {
  margin-bottom: 0.5rem;
}

p.from-them {
  align-items: flex-start;
  background-color: #e5e5ea;
  color: #000;
}

p.from-them:before {
  border-top-right-radius: 0.8rem 0.7rem;
  border-left: 1rem solid #e5e5ea;
  left: -0.35rem;
  top: 0rem;
  transform: translate(0, -0.1rem);
}

p.from-them::after {
  background-color: rgb(255, 255, 255);
  border-top-right-radius: 0.5rem;
  left: 20px;
  top: 0rem;
  transform: translate(-30px, -0px);
  width: 10px;
}

p[class^="from-"].emoji {
  background: none;
  font-size: 2.5rem;
}

p[class^="from-"].emoji::before {
  content: none;
}

.no-tail::before {
  display: none;
}

.margin-b_none {
  margin-bottom: 0 !important;
}

.margin-b_one {
  margin-bottom: 1rem !important;
}

.margin-t_one {
  margin-top: 1rem !important;
}
/*general styling*/
@font-face {
  font-family: "SanFrancisco";
  src:
    url("https://cdn.rawgit.com/AllThingsSmitty/fonts/25983b71/SanFrancisco/sanfranciscodisplay-regular-webfont.woff2") format("woff2"),
    url("https://cdn.rawgit.com/AllThingsSmitty/fonts/25983b71/SanFrancisco/sanfranciscodisplay-regular-webfont.woff") format("woff");
}



.comment {
  color: #222;
  font-size: 1.25rem;
  line-height: 1.5;
  margin-bottom: 1.25rem;
  max-width: 100%;
  padding: 0;
}
